@import url("../../color.css");

.LinkList {
  width: 338px;
  margin: 0px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
}

.list {
  display: flex;
  align-items: center;
  padding: 15px 10px;
  background-color: var(--common);
  box-shadow: 0px 3px 6px var(--shadow);
  width: 338px;
  margin-bottom: 20px;
}
.body {
  text-align: left;
  margin-left: 12px;
}
.icon {
  width: 36px;
}

.title {
  font-size: 16px;
  color: var(--main-secondry);
  font-weight: bold;
}
.detail {
  font-size: 12px;
  color: var(--sub-common);
}
