@import url("../../color.css");
.EditLinkList {
  width: 338px;
  margin: 0px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
}
.list {
  display: flex;
  align-items: center;
  padding: 15px 10px;
  background-color: var(--common);
  box-shadow: 0px 3px 6px var(--shadow);
  width: 338px;
  margin-bottom: 20px;
}
.body {
  text-align: left;
  margin-left: 12px;
  width: 250px;
}

.toggle {
  width: 17px;
}

.title {
  font-size: 16px;
  color: var(--main-secondry);
  font-weight: bold;
}
.edit-link-detail {
  font-size: 12px;
  color: var(--main-secondry);
  font-family: "Noto Sans CJK JP,Regular";
}
.url {
  font-size: 8pt;
  color: var(--main-secondry);
  font-family: "Noto Sans CJK JP,Light";
}

.next-icon {
  width: 27px;
  margin-right: 10px;
}
.next {
  height: 18px;
}
