@import url("../../color.css");

.listCard {
  width: 344px;
  padding: 15px 10px;
  background-color: var(--common);
  box-shadow: 0px 3px 6px var(--shadow);
  border-radius: 10px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 0;
}

.listMenu {
  display: flex;
  align-items: center;
}
.listMenu img {
  width: 22px;
  height: 22px;
}
.listMenu div {
  width: 272px;
  margin-left: 20px;
  text-align: left;
  font-size: 16px;
  font-family: "Noto Sans CJK JP,Regular";
  color: var(--main-secondry);
}
.listMenu:not(:last-child) {
  margin-bottom: 30px;
}
.next {
  height: 18px;
}
