@import url("../../color.css");

.card {
  width: 344px;
  margin-left: auto;
  margin-right: auto;
}
/* カード上部 */

.cardHeader {
  width: 344px;
  height: 56px;
  background-size: cover;
  display: flex;
  align-items: center;
  border-radius: 8px 8px 0 0;
  box-shadow: 0px 3px 6px var(--shadow);
  position: relative;
  z-index: 10;
}
.cardIcon {
  width: 50px;
  height: 50px;
  margin-left: 5px;
  margin-top: 3px;
  object-fit: cover;
  border-radius: 8px;
  box-shadow: 0px 3px 6px var(--shadow);
}
.cardBody {
  width: 245px;
  text-align: left;
  padding-left: 13px;
  padding-top: 8px;
  padding-bottom: 8px;
  font-family: "Noto Sans CJK JP,Regular";
}
.cardBody h2 {
  font-size: 16px;
  color: var(--main-secondry);
  margin: 0;
  padding: 0;
  font-weight: lighter;
}
.cardBody p {
  font-size: 12px;
  color: var(--common);
  margin: 0;
  padding: 0;
}

.cardAction {
  display: block;
  height: 20px;
  padding: 0;
  margin: 0;
}
.cardFooter {
  width: 344px;
  height: 40px;
  background-color: var(--common);
  box-shadow: 0px 3px 6px var(--shadow);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.cardFooter img {
  height: 17.14px;
  width: 17.14px;
}
.cardFooter :first-child {
  margin-left: 33px;
}
.cardFooter :last-child {
  margin-right: 33px;
}
